.pain-card{
  text-align: center;
  &__img{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin: 0 auto;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title{
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    margin: 12px auto 0;
    font-family: $font2;
    max-width: 280px;
  }
}

.how-card{
  &__content{
    border: 6px solid #E1ECFF;
    border-radius: 15px;
    padding: 21px 25px;
    font-size: 16px;
    line-height: 24px;
    span{
      color: #3CC39D;
      font-weight: 700;
    }
  }
  @media screen and (min-width: 992px){
    position: relative;
    &__img{
      position: absolute;
    }
    &:nth-child(1){
      margin-bottom: 100px;
      .how-card__content{
        max-width: 790px;
        margin: 0 130px 0 auto;
        padding-right: 90px;
      }
      .how-card__img{
        right: 0;
        top: 50%;
        transform: translateY(-40%);
      }
    }
    &:nth-child(2){
      margin-bottom: 209px;
      .how-card__content{
        max-width: 545px;
        margin: 0 auto 0 130px;
        padding-left: 50px;
        text-align: right;
      }
      .how-card__img{
        top: 0;
        left: 0;
      }
    }
    &:nth-child(3){
      margin-bottom: 61px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      align-items: center;
      gap: 20px;
      .how-card__content{
        max-width: 567px;
        margin-left: auto;
        padding: 20px;
      }
      .how-card__img{
        position: static;
        max-width: 100%;
        width: 551px;
        margin-right: auto;
      }
    }
    &:nth-child(4){
      max-width: 800px;
      .how-card__content{
        margin: 0 auto 0 0;
      }
      .how-card__img{
        bottom: -90px;
        right: -136px;
      }
    }
  }
}

.feedback-card{
  padding: 18px 20px;
  background: #FFFFFF;
  border-radius: 10px;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__user{
    display: flex;
    align-items: center;
  }
  &__avatar{
    flex-shrink: 0;
    width: 40px;
    height: 40px;
    margin-right: 18px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }
  &__name{
    font-weight: 500;
    font-size: 16px;
  }
  &__date{
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    color: #9F9F9F;
    margin-top: 4px;
  }
  &__shop{
    &-title{
      font-weight: 500;
      font-size: 11px;
      line-height: 14px;
      color: #9F9F9F;
    }
    &-img{
      display: block;
      margin-left: auto;
      max-width: 65px;
    }
  }
  &__text{
    margin-top: 12px;
    font-size: 14px;
    line-height: 20px;
  }
  &__link{
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    margin-top: 10px;
    display: inline-block;
    text-decoration-line: underline;
    color: $primary;
  }
  &__images{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
    margin-top: 18px;
  }
  &__img{
    &-wrap{
      display: block;
      position: relative;
      padding-bottom: 100%;
      width: 100%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 10px;
      }
    }
    &-btn{
      border: 2px solid rgba(0, 159, 223, 0.2);
      border-radius: 10px;
      &-text{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 100%;
        font-weight: 700;
        font-size: 15px;
        line-height: 20px;
        color: #009FDF;
        text-align: center;
        display: block;
      }
      &:hover{
        border-color: $primary;
      }
    }
  }
}

.faq-card{
  border: 6px solid #E1ECFF;
  border-radius: 15px;
  padding: 17px 30px;
  cursor: pointer;
  &._active{
    border-color: $secondary;
    .faq-card__btn{
      transform: rotate(180deg);
      path{
        fill: $secondary;
      }
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
  }
  &__btn{
    flex-shrink: 0;
    margin-left: 20px;
  }
  &__hidden{
    margin-top: 14px;
    display: none;
  }
  &__desc{
    font-size: 17px;
    line-height: 24px;
    max-width: 1020px;
    a{
      color: #029DDA;
    }
  }
  &__title2{
    text-transform: uppercase;
    font-size: 24px;
  }
  &__list{
    column-count: 2;
    column-gap: 20px;
    font-size: 14px;
    line-height: 18px;
    li{
      &:not(:last-child){
        margin-bottom: 10px;
      }
      &:before{
        width: 8px;
        height: 8px;
        transform: translateY(4px);
      }
    }
  }
}

.faq-card2{
  padding: 30px 38px;
  .faq-card__btn{
    svg{
      width: 25px;
      height: auto;
    }
  }
}

.result-card{
  padding: 15px 20px;
  background: #FFFFFF;
  border-radius: 8px;
  &__title{
    font-weight: 600;
    font-size: 17px;
    line-height: 20px;
    color: $primary;
  }
  &__media{
    margin-top: 17px;
    position: relative;
    padding-bottom: 81.396%;
  }
  &__images{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1.9fr 1fr;
    gap: 18px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__images2{
    grid-template-columns: 1fr 1fr;
    gap: 18px 20px;
  }
  &__desc{
    font-size: 15px;
    line-height: 20px;
    margin-top: 13px;
  }
}


@media screen and (max-width: 992px){
  .how-card{
    &__img{
      height: 200px;
      margin: 0 auto;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        max-width: 100%;
        max-height: 100%;
      }
    }
    &__content{
      font-size: 14px;
      line-height: 24px;
      padding: 20px;
    }
  }

  .faq-card{
    padding: 18px 25px;
    border-width: 4px;
    &__title,
    &__desc{
      font-size: 14px;
      line-height: 24px;
    }
    &__hidden{
      margin-top: 5px;
    }
    &__list{
      column-count: 1;
    }
    &__title2{
      font-size: 18px;
      line-height: 24px;
    }
  }

  .faq-card2{
    padding-left: 25px;
    padding-right: 25px;
  }

  .result-card{
    &__desc{
      font-size: 14px;
      line-height: 20px;
      margin-top: 12px;
    }
    &__media{
      margin-top: 6px;
    }
    &__images{
      gap: 13px;
    }
  }
}


@media screen and (max-width: 576px){
  .pain-card{
    &__img{
      width: 120px;
      height: 120px;
    }
    &__title{
      font-size: 15px;
    }
  }
}
