.main{
  background-image: url(../img/main-img.jpg);
  background-size: cover;
  background-position: center;
  padding: 87px 0 112px;
  &__title{
    font-weight: 900;
    font-size: 60px;
    line-height: 120%;
    text-transform: uppercase;
    color: $primary;
    font-family: $font1;
  }
  &__subtitle{
    font-family: $font1;
    font-weight: 700;
    font-size: 36px;
    line-height: 43px;
    margin-top: 16px;
    text-transform: uppercase;
  }
  &__desc{
    font-size: 18px;
    line-height: 23px;
    margin-top: 17px;
    max-width: 540px;
    br{
      display: none;
    }
  }
  &__btn{
    width: 246px;
    height: 47px;
    margin-top: 29px;
    font-weight: 700;
    font-size: 15px;
    text-transform: uppercase;
  }
}

.pain{
  padding: 44px 0 20px;
  &__title{
    margin: 0 auto;
    max-width: 575px;
    text-align: center;
  }
  &__grid{
    margin: 51px auto 0;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 55px 20px;
    max-width: 1090px;
    width: 100%;
  }
  &__img{
    margin: 82px auto 0;
    width: 417px;
    max-width: 100%;
    display: block;
  }
}

.safe{
  padding: 20px 0 33px;
  &__title{
    text-align: center;
    max-width: 671px;
    margin: 0 auto;
    padding-bottom: 22px;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 47px;
      height: 5px;
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
      background: #3CC39D;
    }
  }
  &__desc{
    font-size: 24px;
    line-height: 30px;
    margin-top: 19px;
    text-align: center;
    text-transform: uppercase;
  }
  &__img{
    display: block;
    width: 100%;
    max-width: 934px;
    margin: 13px auto 0;
  }
}

.advantages{
  margin: 33px 0 35px;
  background: linear-gradient(90deg, #1A9ED7 0.73%, #42C1A1 99.27%);
  padding: 120px 0;
  color: #fff;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__item{
    position: relative;
    min-width: 161px;
  }
  &__title{
    text-align: center;
    font-weight: 700;
    font-size: 72px;
    line-height: 86px;
    text-transform: uppercase;
  }
  &__desc{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    font-weight: 700;
    font-size: 19px;
    line-height: 24px;
    padding: 0 6px;
    text-transform: uppercase;
    background: #006884;
    border-radius: 3px;
    height: 30px;
    display: flex;
    align-items: center;
    white-space: nowrap;
  }
  &__desc1{
    top: 100%;
    margin-top: 2px;
  }
  &__desc2{
    bottom: 100%;
    margin-bottom: 5px;
  }
}

.what{
  padding: 35px 0;
  &__inner{
    display: grid;
    grid-template-columns: 51.368% auto;
    gap: 6%;
    align-items: center;
  }
  &__content{
    position: relative;
    padding: 25px 25px 30px 50px;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 11px solid #eff5ff;
      pointer-events: none;
      z-index: 2;
      border-radius: 20px;
    }
  }
  &__decor{
    position: absolute;
    top: 0;
    right: 0;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    z-index: 2;
  }
  &__subtitle{
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    font-family: $font2;
    text-transform: uppercase;
    color: $primary;
  }
  &__desc{
    position: relative;
    z-index: 2;
    max-width: 515px;
    margin-top: 20px;
    font-size: 16px;
    line-height: 24px;
    p{
      line-height: inherit;
      &:not(:last-child){
        margin-bottom: 23px;
      }
    }
  }
  &__img{
    margin-top: 20px;
    display: block;
    width: 100%;
  }
  &__info{
    max-width: 515px;
    position: relative;
  }
  &__question{
    position: absolute;
    right: 31px;
    top: 50%;
    transform: translateY(-50%);
    z-index: -1;
  }
  &__title{
    font-size: 40px;
    line-height: 48px
  }
  &__desc2{
    font-size: 20px;
    line-height: 25px;
    margin-top: 20px;
  }
}

.mattress{
  margin: 35px 0 24px;
  padding: 58px 0;
  background: #EFF5FF;
  &__inner{
    display: grid;
    grid-template-columns: 54.018% auto;
    gap: 30px 20px;
    align-items: flex-end;
  }
  &__title{
    span{
      color: #3CC39D;
    }
  }
  &__desc{
    margin-top: 22px;
  }
  &__items{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    max-width: 494px;
    width: 100%;
    margin-left: auto;
  }
  &__img{
    padding-bottom: 85.24%;
    position: relative;
    img{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      max-height: 100%;
      max-width: 100%;
    }
  }
  &__subtitle{
    font-size: 16px;
    line-height: 19px;
    margin-top: 1px;
    font-weight: 700;
    font-family: $font2;
    text-align: center;
  }
}

.how-work{
  padding: 24px 0 26px;
  &__inner{
    display: grid;
    grid-template-columns: 43.6% 43.6%;
    align-items: center;
    justify-content: space-between;
  }
  &__desc,
  &__desc2{
    margin-top: 23px;
  }
  &__desc{
    font-weight: 500;
  }
  &__img{
    width: 100%;
    display: block;
    &--mob{
      display: none;
    }
  }
  &__items{
    margin-top: 16px;
    display: flex;
    justify-content: space-between;
  }
  &__item{
    &-img{
      padding-bottom: 100%;
      border-radius: 50%;
      overflow: hidden;
      position: relative;
      img{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    &-title,
    &-desc{
      text-align: center;
      font-size: 16px;
      line-height: 125%;
    }
    &-title{
      margin-top: 23px;
      font-weight: 900;
    }
    &-desc{
      font-weight: 500;
    }
  }
  &__arrow{
    padding-top: 54px;
  }
}

.consult{
  margin: 26px 0;
  padding: 50px 0 57px;
  background: #006795;
  border-radius: 5px;
  color: #fff;
  &__inner{
    max-width: 680px;
    width: 100%;
    margin: 0 auto;
  }
  &__title{
    text-align: center;
  }
  &__form{
    margin-top: 30px;
    display: grid;
    grid-template-columns: 1fr 1fr .73fr;
    gap: 19px;
  }
  &__btn{
    height: 50px;
    font-weight: 700;
    font-size: 13px;
    text-transform: uppercase;
    border-radius: 5px;
  }
  &__hint{
    font-size: 14px;
    line-height: 18px;
    margin-top: 13px;
    a{
      text-decoration: underline;
      white-space: nowrap;
    }
  }
  &__subtitle{
    font-size: 22px;
    line-height: 36px;
    text-transform: uppercase;
    text-align: center;
    margin-top: 1px;
  }
}

.how{
  padding: 25px 0 45px;
  &__title{
    text-align: center;
  }
  &__desc{
    text-align: center;
    font-size: 16px;
    line-height: 150%;
    margin-top: 21px;
  }
  &__steps{
    margin-top: 87px;
  }
  @media screen and (min-width: 992px){
    .swiper{
      &-wrapper{
        display: block;
        transform: unset !important;
      }
      &-button,
      &-pagination{
        display: none;
      }
    }
  }
  &__warning{
    padding: 37px 45px;
    margin-top: 105px;
    background: linear-gradient(90deg, #1A9ED7 1.03%, #42C1A2 100%);
    border-radius: 5px;
    display: flex;
    align-items: center;
    &-img{
      width: 63px;
      flex-shrink: 0;
      margin-right: 45px;
      img{
        width: 100%;
      }
    }
    &-text{
      font-size: 16px;
      line-height: 20px;
      color: #fff;
    }
  }
  &__btn{
    margin: 50px auto 0;
    width: 286px;
    max-width: 100%;
    height: 50px;
    font-weight: 700;
    font-size: 13px;
    text-transform: uppercase;
    img{
      margin-right: 15px;
    }
  }
  &__img{
    margin: 85px auto 0;
    width: 100%;
    max-width: 771px;
    display: block;
  }
}

.time{
  margin-top: 43px;
  display: flex;
  justify-content: space-between;
  &__from,
  &__to{
    font-size: 16px;
    line-height: 125%;
    text-align: center;
    font-weight: 700;
    span{
      display: block;
      font-size: 60px;
      line-height: 62px;
    }
  }
  &__grid{
    flex-grow: 1;
    margin: 0 45px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
  &__item{
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    &:last-child{
      .time__line{
        &:after{
          right: 0;
          left: unset;
          transform: translate(50%,-50%);
        }
      }
      .time__info{
        border-right: 1px dashed rgba($black,.4);
      }
      .time__subtitle{
        left: unset;
        right: 0;
        text-align: right;
      }
    }
  }
  &__minutes{
    background-color: #c0e1f8;
    padding: 19px 14px;
    border-radius: 15px;
    font-weight: 700;
    line-height: 125%;
    text-align: center;
    color: $primary;
    position: relative;
    span{
      font-size: 24px;
      display: block;
    }
    &:before{
      content: '';
      position: absolute;
      width: 20px;
      height: 20px;
      border-radius: 4px;
      background-color: inherit;
      bottom: 0;
      left: 50%;
      transform: translate(-50%,50%) rotate(-45deg);
    }
  }
  &__line{
    margin-top: 30px;
    height: 4px;
    width: 100%;
    background-color: $primary;
    position: relative;
    &:before,
    &:after{
      content: '';
      position: absolute;
      width: 18px;
      height: 18px;
      top: 50%;
      left: 0;
      transform: translate(-50%,-50%);
      border: 5px solid $primary;
      background-color: #fff;
      border-radius: 50%;
    }
    &:after{

    }
  }
  &__info{
    padding: 25px 17px 17px;
    flex-grow: 1;
    border-left: 1px dashed rgba($black,.4);
    text-align: center;
  }
  &__title{
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: $primary;
  }
  &__desc{
    max-width: 285px;
    font-size: 14px;
    line-height: 18px;
    margin: 4px auto 0;
  }
  &__subtitle{
    position: absolute;
    top: 38px;
    left: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
  }
}

.feedback{
  margin: 45px 0 26px;
  padding: 51px 0 24px;
  background: #EFF5FF;
  &__title{
    text-align: center;
    font-weight: 900;
    font-size: 35px;
    line-height: 42px;
    font-family: $font1;
    text-transform: uppercase;
  }
  &__slider{
    margin-top: 42px;
    overflow: hidden;
  }
  .swiper{
    &-pagination{
      position: static;
      margin-top: 24px;
      display: flex;
    }
  }
}

.what-get{
  padding: 26px 0 30px;
  &__title{
    text-align: center;
    padding-bottom: 10px;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 47px;
      height: 5px;
      background-color: $secondary;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  &__subtitle{
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    text-transform: uppercase;
    margin-top: 9px;
  }
  &__grid{
    margin-top: 63px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__img{
    flex-grow: 1;
    max-width: 627px;
    img{
      width: 100%;
    }
  }
  &__items{
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-shrink: 0;
    &:last-child{
      .what-get__item{
        margin-left: auto;
        margin-right: 0;
      }
    }
  }
  &__item{
    border: 4px solid #E1ECFF;
    border-radius: 15px;
    padding: 0 25px;
    display: flex;
    align-items: center;
    white-space: nowrap;
    height: 58px;
    font-weight: 700;
    font-size: 13px;
    line-height: 16px;
    text-transform: uppercase;
    color: $primary;
    margin-right: auto;
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }
}

.consult2{
  background-image: url(../img/consult-bg.webp);
  background-size: cover;
  background-position: center;
  padding: 51px 0 0;
  margin: 30px 0 25px;
  &__title{
    text-align: center;
  }
  &__subtitle{
    font-size: 22px;
    line-height: 28px;
    margin-top: 9px;
    text-transform: uppercase;
    text-align: center;
  }
  &__form{
    width: 820px;
    max-width: 100%;
    margin: 55px auto 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
  }
  &__btn{
    grid-column: 2/3;
    height: 50px;
    font-weight: 700;
    font-size: 13px;
    text-transform: uppercase;
  }
  .fg{
    label{
      position: absolute;
      top: 50%;
      left: 29px;
      transform: translateY(-50%);
      font-weight: 500;
      font-size: 13px;
      line-height: 16px;
      text-transform: uppercase;
    }
    input{
      padding-left: 106px;
      &::placeholder{
        color: #838383;
      }
      &:focus{
        &::placeholder{
          color: transparent;
        }
      }
    }
  }
  &__img{
    margin: 19px auto 0;
    display: block;
    width: 100%;
    max-width: 984px;
  }
}

.clients{
  padding: 22px 0 49px;
  &__title{
    text-align: center;
  }
  &__slider{
    margin-top: 46px;
    padding: 0 60px;
    position: relative;
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      display: flex;
      align-items: center;
      justify-content: center;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-button{
      &-prev{
        left: 0;
      }
      &-next{
        right: 0;
      }
    }
    &-pagination{
      display: none;
    }
  }
}

.author{
  background-image: url(../img/author-bg.webp);
  background-size: cover;
  background-position: center;
  padding: 130px 0 0;
  margin: 49px 0 82px;
  &__inner{
    display: grid;
    grid-template-columns: 60.685% 39.315%;
  }
  &__content{
    background: rgba(255, 255, 255, 0.9);
    border-radius: 0px 5px 0px 0px;
    padding: 40px 45px 50px;
    position: relative;
  }
  &__badge{
    background: #029DDA;
    border-radius: 7px 7px 0px 0px;
    position: absolute;
    bottom: 100%;
    left: 0;
    color: #fff;
    padding: 0 32px;
    display: flex;
    align-items: center;
    height: 60px;
    font-weight: 900;
    font-size: 18px;
    line-height: 22px;
    text-transform: uppercase;
    font-family: $font1;
  }
  &__subtitle{
    text-transform: uppercase;
    font-family: $font1;
    font-weight: 700;
    color: #029DDA;
  }
  &__list{
    margin-top: 8px;
  }
  &__img{
    position: relative;
    img{
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 2;
      width: 120.45%;
      margin-left: -20.45%;
    }
  }
}

.set{
  padding: 82px 0 24px;
  &__inner{
    display: grid;
    grid-template-columns: 56.5% auto;
    gap: 12px;
    align-items: center;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__list{
    margin-top: 10px;
    font-size: 18px;
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    li{
      line-height: 120%;
      &:before{
        transform: translateY(8px);
      }
    }
  }
}

.test{
  padding: 55px 0 16px;
  margin: 24px 0;
  background-image: url(../img/test-bg.webp);
  background-size: cover;
  background-position: center;
  &__subtitle{
    text-align: center;
    font-size: 22px;
    line-height: 28px;
    text-transform: uppercase;
  }
  &__title{
    text-align: center;
    margin-top: 6px;
  }
  &__btn{
    margin: 16px auto 0;
    width: 262px;
    height: 50px;
    font-weight: 700;
    font-size: 13px;
    text-transform: uppercase;
  }
  &__img{
    display: block;
    margin: 67px auto 0;
    width: 100%;
    max-width: 1038px;
  }
}

.faq{
  padding: 24px 0 30px;
  &__desc{
    margin-top: 23px;
    font-size: 16px;
    line-height: 20px;
  }
  &__items{
    margin-top: 20px;
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
  }
}

.consult3{
  margin-bottom: 0;
  .consult{
    &__inner{
      max-width: 100%;
    }
    &__form{
      margin: 42px auto 0;
      width: 830px;
      max-width: 100%;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 10px;
    }
    &__hint{
      text-align: center;
    }
  }
}


@media screen and (max-width: 1100px){
  .time{
    &__item{
      &:last-child{
        .time__subtitle{
          margin: 0 -30px 0 0;
        }
      }
    }
    &__subtitle{
      margin-left: -30px;
    }
  }
}


