        width: 85%;
        padding-bottom: 85%;
        margin: 0 auto;
      }
      &-title{
        margin-top: 13px;
      }
    }
  }

  .how{
    overflow: hidden;
    padding: 22px 0 26px;
    .swiper{
      &-container{
        overflow: hidden;
      }
      &-button{
        top: 52px;
        margin: 0;
        &-prev{
          left: 0;
        }
        &-next{
          right: 0;
        }
      }
      &-pagination{
        position: static;
        margin-top: 27px;
        display: flex;
      }
    }
    &__steps{
      position: relative;
    }
    &__warning{
      margin-top: 44px;
      padding: 20px;
      flex-direction: column;
      text-align: center;
      &-img{
        width: 58px;
        margin: 0 0 20px;
      }
      &-text{
        font-weight: 500;
        font-size: 14px;
      }
    }
    &__btn{
      margin-top: 34px;
    }
    &__img{
      display: none;
    }
  }

  .time{
    flex-direction: column;
    &__grid{
      margin: 36px 0;
      grid-template-columns: 100%;
    }
    &__from,
    &__to{
      margin-right: auto;
      span{
        font-size: 40px;
        line-height: 42px;
      }
    }
    &__item{
      display: block;
      border-top: 1px dashed rgba(0,0,0,.4);
      padding: 35px;
      &:last-child{
        border-bottom: 1px dashed rgba(0,0,0,.4);
        .time__subtitle{
          bottom: unset;
          top: 100%;
          margin: 10px 0 0;
          right: unset;
          left: 0;
        }
        .time__line{
          &:after{
            top: unset;
            left: 0;
            bottom: 0;
            transform: translate(-50%,50%);
          }
        }
      }
    }
    &__minutes{
      position: absolute;
      top: 18px;
      right: 0;
      font-size: 12px;
      line-height: 15px;
      padding: 7px;
      border-radius: 10px;
      &:before{
        display: none;
      }
      span{
        font-size: 22px;
        line-height: 28px;
      }
    }
    &__info{
      padding: 0;
      border: none !important;
      text-align: left;
    }
    &__line{
      position: absolute;
      left: 0;
      top: 0;
      margin: 0;
      height: 100%;
      width: 4px;
      transform: translateX(-50%);
      &:before,
      &:after{
        top: 0;
        left: 0;
      }
    }
    &__desc{
      margin-left: 0;
    }
    &__subtitle{
      top: unset;
      bottom: 100%;
      padding: 0 40px;
      margin: 0 0 10px;
      br{
        display: none;
      }
    }
  }

  .feedback{
    padding: 46px 0 50px;
    margin: 26px 0 23px;
    &__title{
      font-size: 22px;
      line-height: 26px;
    }
    &__slider{
      margin-top: 16px;
    }
  }

  .what-get{
    padding: 23px 0 25px;
    &__title{
      padding-bottom: 15px;
    }
    &__subtitle{
      font-size: 16px;
      line-height: 20px;
      margin-top: 11px;
    }
    &__grid{
      margin: 30px auto 0;
      display: block;
      max-width: 600px;
    }
    &__item{
      padding: 0 17px;
      font-size: 12px;
      height: 47px;
      &:not(:last-child){
        margin-bottom: 10px;
      }
      &:nth-child(odd){
        margin-left: 0 !important;
        margin-right: auto !important;
      }
      &:nth-child(even){
        margin-left: auto !important;
        margin-right: 0 !important;
      }
    }
    &__img{
      margin: 30px 0 15px;
    }
  }

  .clients{
    padding: 23px 0 25px;
    &__slider{
      margin-top: 17px;
    }
  }

  .author{
    padding-top: 40px;
    margin: 25px 0 23px;
    &__inner{
      grid-template-columns: 100%;
    }
    &__img{
      order: -1;
      pointer-events: none;
      img{
        position: static;
        width: calc(100% - 71px);
        display: block;
        max-width: 500px;
        margin-left: auto;
      }
    }
    &__badge{
      font-size: 16px;
      padding: 0 40px;
      z-index: 2;
    }
    &__content{
      padding: 25px;
      z-index: 2;
    }
    &__subtitle{
      font-size: 16px;
      line-height: 19px;
    }
    &__list{
      margin-top: 14px;
    }
  }

  .set{
    padding: 23px 0 10px;
    &__inner{
      grid-template-columns: 100%;
    }
    &__content{
      order: -1;
    }
    &__list{
      font-size: 14px;
      margin-top: 7px;
      li{
        &:before{
          transform: translateY(2px);
        }
      }
    }
    &__img{
      width: calc(100% + 34px);
      margin-left: -17px;
    }
  }

  .test{
    padding: 46px 0 24px;
    margin: 10px 0 23px;
    &__subtitle{
      font-size: 16px;
      line-height: 20px;
    }
    &__title{
      margin-top: 3px;
    }
    &__btn{
      width: 252px;
      margin-top: 16px;
      max-width: 100%;
    }
    &__img{
      margin-top: 30px;
    }
  }

  .faq{
    padding: 23px 0 20px;
    &__desc{
      margin-top: 15px;
      font-size: 14px;
      line-height: 24px;
    }
    &__items{
      gap: 10px;
    }
  }
}


@media screen and (max-width: 768px){
  .advantages{
    &__inner{
      display: block;
    }
    &__item{
      display: flex;
      flex-direction: column;
      align-items: center;
      &:not(:last-child){
        margin-bottom: 21px;
        padding-bottom: 38px;
        &:before{
          content: '';
          position: absolute;
          background: #3CC39D;
          width: 49.21px;
          height: 5px;
          bottom: 0;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
    &__desc{
      position: static;
      margin: 0 auto;
      transform: unset;
    }
  }

  .consult{
    margin: 22px 0;
    padding: 38px 0;
    &__form{
      grid-template-columns: 100%;
      gap: 17px;
      margin-top: 21px;
    }
    &__hint{
      margin-top: 22px;
      text-align: center;
    }
    &__subtitle{
      font-size: 16px;
      line-height: 20px;
      margin: 13px auto 0;
      max-width: 250px;
    }
  }

  .consult2{
    margin: 25px 0 23px;
    padding: 47px 0 56px;
    &__title{
      max-width: 205px;
      margin: 0 auto;
    }
    &__subtitle{
      font-size: 16px;
      line-height: 20px;
      margin: 10px auto 0;
      max-width: 170px;
    }
    &__form{
      grid-template-columns: 100%;
      margin-top: 29px;
      gap: 15px;
    }
    &__btn{
      margin-top: 4px;
      grid-column: 1/2;
      width: 100%;
    }
    &__img{
      display: none;
    }
  }

  .consult3{
    margin-bottom: 0;
    .consult{
      &__title{
        max-width: 200px;
        margin: 0 auto;
      }
      &__form{
        margin-top: 34px;
        grid-template-columns: 100%;
        gap: 17px;
      }
      &__hint{
        text-align: center;
      }
      &__btn{
        width: 100%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    background-position: right 55% center;
    background-size: auto 100%;
    &__content{
      max-width: 225px;
    }
    &__desc{
      br{
        display: unset;
      }
    }
  }

  .pain{
    &__grid{
      grid-template-columns: 1fr;
      gap: 30px;
    }
    &__img{
      width: 251px;
      margin-top: 35px;
    }
  }

  .what{
    padding-bottom: 80px;
    &__content{
      padding-bottom: 70px;
    }
    &__top{
      //position: static;
      padding-right: 70px;
    }
    &__plus{
      top: -15px;
      right: -15px;
      z-index: 3;
      width: 60px;
      align-self: flex-start;
      position: absolute;
    }
    &__decor{
      width: 118px;
    }
    &__img{
      width: calc(100% - 32px);
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translate(-50%,-50%);
      z-index: 3;
      margin: 0;
    }
  }

  .clients{
    .swiper-pagination{
      display: flex;
      position: static;
      margin-top: 14px;
    }
  }
}
