.display1,
.display2{
  font-weight: 900;
  text-transform: uppercase;
  font-family: $font1;
}

.display1{
  font-size: 40px;
  line-height: 48px;
}

.display2{
  font-size: 38px;
  line-height: 46px;
}

.accent-text{
  color: $primary;
}

.secondary-text{
  color: #38C1A3;
}

.body-text{
  font-size: 18px;
  line-height: 23px;
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 10px;
    }
  }
}

.m-list{
  font-size: 15px;
  line-height: 167%;
  li{
    line-height: inherit;
    display: flex;
    &:before{
      content: '';
      display: block;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      flex-shrink: 0;
      background: #42C1A1;
      margin-right: 13px;
      transform: translateY(7px);
    }
  }
}


@media screen and (max-width: 992px){
  .display1,
  .display2{
    font-size: 22px;
    line-height: 26px;
  }

  .body-text{
    font-size: 14px;
    line-height: 24px;
  }

  .m-list{
    font-size: 14px;
    line-height: 18px;
    li{
      &:before{
        transform: translateY(3px);
      }
    }
  }
}
