.banner{
  img{
    width: 100%;
  }
  &__img2{
    display: none;
  }
}

.pain--blue,
.safe--blue{
  background: #F6FAFF;
}

.history{
  padding: 127px 0;
  &__inner{
    display: grid;
    grid-template-columns: 36.8% auto 36.8%;
  }
  &__img{
    position: relative;
    img{
      position: absolute;
      bottom: -127px;
      left: 50%;
      transform: translateX(-50%);
      width: 164.13%;
    }
  }
  &__subtitle{
    margin-top: 9px;
    font-family: $font1;
    font-weight: 900;
    font-size: 18px;
    line-height: 22px;
    text-transform: uppercase;
    color: $primary;
    max-width: 390px;
  }
  &__list{
    margin-top: 25px;
    display: grid;
    grid-template-columns: 100%;
    gap: 5px;
    li{
      &:before{
        background-color: #fff;
      }
      &:last-child{
        max-width: 300px;
      }
    }
  }
  &__regret{
    color: #fff;
    padding: 27px 30px;
    background: linear-gradient(90deg, #1A9ED7 0.73%, #42C1A1 99.27%);
    border-radius: 5px;
    &-show{
      display: none;
    }
  }
  &__info{
    padding: 30px 24px 30px 40px;
    border: 6px solid #E1ECFF;
    border-radius: 15px;
    text-align: right;
  }
  &__desc{
    margin: 17px 0 0 auto;
    font-size: 14px;
    line-height: 18px;
    max-width: 360px;
  }
}

.problem{
  padding: 49px 0 58px;
  &__title{
    width: 672px;
    max-width: 100%;
    margin: 0 auto;
    text-align: center;
  }
  &__subtitle{
    margin-top: 51px;
    font-size: 24px;
    line-height: 125%;
    font-weight: 500;
  }
  &__desc{
    font-size: 14px;
    line-height: 18px;
    span{
      font-weight: 500;
    }
    p{
      line-height: inherit;
      &:not(:last-child){
        margin-bottom: 15px;
      }
    }
  }
  &__inner{
    display: grid;
    grid-template-columns: 38% auto 39%;
  }
  &__img{
    img{
      width: 150%;
      margin-left: -25%;
    }
  }
  &__left{
    p{
      &:last-child{
        padding-right: 80px;
      }
    }
  }
  &__right{
    text-align: right;
    p{
      &:last-child{
        padding-left: 80px;
      }
    }
  }
  &__btn{
    width: 100%;
    margin-top: 15px;
    padding: 42px 15px;
    text-align: center;
    font-weight: 700;
    font-size: 17px;
    line-height: 21px;
    border-radius: 5px;
  }
}

.relationship{
  padding: 53px 0 46px;
  background: #EFF5FF;
  &__title{
    width: 700px;
    max-width: 100%;
    margin: 0 auto;
    text-align: center;
  }
  &__grid{
    margin-top: 45px;
    display: grid;
    grid-template-columns: 61.54% auto;
    gap: 40px;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__item{
    display: flex;
    align-items: center;
    padding: 10px 0;
    border-top: 1px solid #C1E1F8;
    &:first-child{
      border-top: none;
      padding-top: 0;
    }
    &-num{
      flex-shrink: 0;
      margin-right: 14px;
      width: 34px;
      height: 34px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: #C1E1F8;
      border-radius: 10px;
      font-size: 15px;
      line-height: 24px;
      font-weight: 900;
      font-family: $font1;
      color: #029DDA;
    }
    &-title{
      font-weight: 700;
      font-size: 18px;
      sub{
        font-size: 14px;
      }
    }
  }
  &__desc{
    margin-top: 18px;
    font-size: 15px;
    line-height: 18px;
    br{
      display: none;
    }
  }
  &__desc2{
    margin-top: 58px;
    font-size: 17px;
    line-height: 21px;
  }
}

.interesting{
  margin-top: 30px;
  background-image: url(../img/interesting.webp);
  background-size: cover;
  background-position: center;
  padding: 60px 0;
  color: #fff;
  &__title{
    text-align: center;
  }
  &__desc{
    width: 700px;
    max-width: 100%;
    margin: 12px auto 0;
    font-size: 17px;
    line-height: 24px;
    text-align: center;
  }
  &__btn{
    width: 200px;
    height: 50px;
    margin: 26px auto 0;
    font-weight: 700;
    font-size: 14px;
    text-transform: uppercase;
  }
  &__img{
    margin: -50px auto 0;
    pointer-events: none;
    width: 100%;
    display: block;
    max-width: 1126px;
  }
  &__channels{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    width: 690px;
    max-width: 100%;
    margin: 0 auto;
  }
  &__channel{
    min-height: 120px;
    display: flex;
    align-items: center;
    padding: 10px 26px;
    border: 6px solid #004B6C;
    border-radius: 5px;
    &-icon{
      width: 68px;
      flex-shrink: 0;
      margin-right: 24px;
      img{
        width: 100%;
      }
    }
    &-title{
      font-weight: 700;
      font-size: 14px;
      line-height: 17px;
      text-transform: uppercase;
      font-family: $font1;
    }
    &-img{
      max-width: 100%;
      display: block;
      margin-top: 3px;
    }
  }
}

.use{
  padding: 124px 0 53px;
  background-image: url(../img/use.webp);
  background-size: cover;
  background-position: top right;
  &__subtitle{
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    text-transform: uppercase;
    margin-top: 120px;
  }
  &__list{
    margin-top: 8px;
    width: 780px;
    max-width: 100%;
    column-count: 2;
    font-size: 14px;
    line-height: 18px;
    li{
      &:not(:last-child){
        margin-bottom: 5px;
      }
      &:before{
        width: 8px;
        height: 8px;
        transform: translateY(4px);
      }
    }
  }
  &__img{
    display: none;
  }
}

.ills{
  padding: 60px 0;
  .faq__items{
    margin: 0;
  }
}

.result{
  padding: 52px 0 60px;
  background: #EFF5FF;
  &__title{
    text-align: center;
  }
  &__slider{
    margin-top: 21px;
    display: grid;
    grid-template-columns: 100px calc(100% - 200px) 100px;
    align-items: center;
  }
  .swiper{
    &-container{
      grid-column: 2/3;
      overflow: hidden;
    }
    &-button{
      position: static;
      margin-top: 0;
      &-next{
        margin-left: auto;
      }
    }
    &-pagination{
      position: static;
      margin-top: 38px;
    }
  }
}

.consult2--m0,
.feedback--m0,
.consult--m0{
  margin: 0;
}

.feedback--m0{
  padding: 50px 0;
}

.consult4{
  margin-top: 60px;
  @media screen and (min-width: 992px){
    margin: 60px 0;
    background-color: transparent;
    padding: 0;
    .consult{
      &__inner{
        border-radius: 5px;
        padding: 50px 0 57px;
        background: #006795;
        max-width: 100%;
      }
      &__title,
      &__form,
      &__hint{
        width: 680px;
        max-width: 100%;
        margin-left: auto;
        margin-right: auto;
      }
    }
  }
}

.what2{
  padding-top: 52px;
  &__title{
    text-align: center;
  }
  &__subtitle{
    width: 768px;
    max-width: 100%;
    margin: 10px auto 0;
    font-weight: 500;
    font-size: 17px;
    line-height: 24px;
    text-align: center;
  }
  &__img{
    margin: 25px auto 0;
    max-width: 100%;
    width: 657px;
    display: block;
  }
  &__title2{
    text-align: center;
    margin-top: 21px;
  }
  &__desc{
    margin-top: 8px;
    text-align: center;
    font-size: 15px;
    line-height: 19px;
  }
  &__items{
    margin: 26px auto 0;
    width: 870px;
    max-width: 100%;
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
  }
  &__item{
    padding: 18px 15px;
    border: 4px solid #E1ECFF;
    border-radius: 15px;
    font-size: 15px;
    line-height: 19px;
    text-align: center;
  }
}


@media screen and (max-width: 1600px){
  .use{
    background-position: right 50% top;
  }
}


@media screen and (max-width: 992px){
  .history{
    padding: 46px 0 13px;
    &__inner{
      position: relative;
      grid-template-columns: 100%;
    }
    &__img{
      width: 264px;
      position: absolute;
      top: 112px;
      left: 50%;
      transform: translateX(-50%);
      img{
        width: 100%;
        position: static;
        transform: unset;
      }
    }
    &__name{
      text-align: center;
    }
    &__subtitle{
      margin: 2px auto 0;
      max-width: 300px;
      text-align: center;
    }
    &__regret{
      margin-top: 350px;
      padding: 16px 20px;
      &-show{
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
      }
      &-title{
        font-weight: 700;
        font-size: 22px;
        line-height: 26px;
        text-transform: uppercase;
        padding-right: 20px;
      }
    }
    &__list{
      margin-top: 20px;
      display: none;
    }
    &__info{
      margin-top: 20px;
      text-align: left;
      padding: 23px 18px;
    }
    &__desc{
      margin-top: 8px;
      p{
        &:not(:last-child){
          margin-bottom: 20px;
        }
      }
    }
  }

  .problem{
    padding: 26px 0 50px;
    &__subtitle{
      margin-top: 20px;
      font-size: 20px;
      line-height: 120%;
    }
    &__inner{
      grid-template-columns: 100%;
      gap: 15px;
    }
    &__left{
      p{
        padding: 0 !important;
      }
    }
    &__right{
      text-align: left;
      p{
        padding: 0 !important;
      }
    }
    &__img{
      order: 3;
      img{
        width: 100%;
        margin: 0;
      }
    }
    &__btn{
      margin-top: 30px;
      padding: 21px 15px;
      font-weight: 600;
      font-size: 15px;
      line-height: 22px;
    }
  }

  .relationship{
    padding: 45px 0 41px;
    &__grid{
      margin-top: 29px;
      grid-template-columns: 100%;
      gap: 20px;
    }
    &__desc{
      font-size: 13px;
      line-height: 22px;
    }
    &__desc2{
      margin-top: 20px;
      font-size: 15px;
      line-height: 22px;
    }
  }

  .interesting{
    padding: 46px 0 56px;
    &__desc{
      margin-top: 15px;
    }
    &__btn{
      margin-top: 30px;
    }
    &__img{
      margin-top: 17px;
    }
  }

  .use{
    padding: 46px 0 140px;
    background-position: right 60% bottom;
    &__subtitle{
      margin-top: 20px;
      font-size: 18px;
      line-height: 22px;
      font-weight: 400;
    }
    &__list{
      margin-top: 20px;
      column-count: 1;
      width: 390px;
    }
  }

  .ills{
    padding: 50px 0;
  }

  .result{
    padding: 47px 0 50px;
    &__slider{
      grid-template-columns: 100%;
    }
    .swiper{
      &-container{
        grid-column: 1/2;
      }
      &-button{
        display: none;
      }
      &-pagination{
        margin-top: 30px;
      }
    }
  }

  .what2{
    padding-top: 46px;
    &__subtitle{
      font-size: 15px;
      line-height: 22px;
      margin-top: 15px;
      font-weight: 400;
    }
    &__title{
      span{
        display: block;
      }
    }
    &__title2{
      margin-top: 15px;
      span{
        display: block;
      }
    }
    &__desc{
      font-size: 14px;
      line-height: 17px;
    }
    &__items{
      gap: 10px;
    }
  }

  .consult4{
    margin: 30px 0;
  }
}


@media screen and (max-width: 768px){
  .banner{
    &__img1{
      display: none;
    }
    &__img2{
      display: block;
    }
  }

  .relationship{
    &__desc{
      br{
        display: unset;
      }
      span{
        display: none;
      }
    }
  }

  .interesting{
    &__channels{
      margin-top: 18px;
      gap: 20px;
      width: 330px;
    }
    &__channel{
      flex-direction: column;
      text-align: center;
      padding: 14px;
      &-icon{
        margin: 0 auto 10px;
      }
      &-title{
        font-size: 14px;
        line-height: 17px;
      }
    }
  }

  .use{
    background-image: none;
    padding-bottom: 0;
    &__img{
      display: block;
      margin-left: -17px;
      width: calc(100% + 34px);
    }
  }
}
