@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    border-radius: 5px;
    transition: background 150ms, border-color 150ms, box-shadow 150ms;
    background: linear-gradient(90deg,#009bdc 7.09%,#3cc39d 93.66%);
    box-shadow: 0 2px 10px 1px rgba(0,0,0,0.15);
    color: #fff;
    &:hover{
        background: linear-gradient(90deg,#006590 7.09%,#2a896e 93.66%) !important;
        color: #fff;
    }
}
